import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, AlertController, ToastController } from 'ionic-angular';
import { AuthProvider } from '../../providers/auth/auth';
import { AdminProvider } from '../../providers/admin/admin';

@IonicPage()
@Component({
  selector: 'page-admin-home',
  templateUrl: 'admin-home.html',
})
export class AdminHomePage {
  userCount: any;
  buySellPetCount: any;
  lostPetsCount: any;
  vaccineSchedCount: any;
  pageLoaded: boolean = false;

  constructor(
    public navCtrl: NavController, 
    public navParams: NavParams, 
    public authProvider: AuthProvider,
    public alertCtrl: AlertController,
    public toastCtrl: ToastController,
    public adminProvider: AdminProvider
  ) {
    this.registeredUserCount();
  }

  registeredUserCount() {
    this.adminProvider.registeredUserCount().then(count01 => {
      this.userCount = count01;
      this.adminProvider.buySellPetCount().then(count02 => {
        this.buySellPetCount = count02;
        this.adminProvider.lostPetCount().then(count03 => {
          this.lostPetsCount = count03;
          this.adminProvider.vaccineSchedCount().then(count4 => {
            this.vaccineSchedCount = count4;
            this.pageLoaded = true;
          }).catch(err => {
            console.log('err', err);
          })
        }).catch(err => {
          console.log('err', err);
        });
      }).catch(err => {
        console.log('err', err);
      });
    }).catch(err => {
      console.log('err', err);
    });
  }

  pushPage(page) {
    this.navCtrl.push(page);
  }

  logout() {
    let confirm = this.alertCtrl.create({
      title: 'Logout Confirmation?',
      message: 'Are you sure do you want to log out?',
      buttons: [
        {
          text: 'Cancel',
          handler: () => {
          }
        },
        {
          text: 'Ok',
          handler: () => {
            this.authProvider.logout().then(() => {
              localStorage.clear();
              this.navCtrl.push('HomePage');
            }).catch(err => {
              let toast = this.toastCtrl.create({
                message: err.message,
                duration: 5000,
                position: 'bottom'
              });
        
              toast.present();
            });
          }
        }
      ]
    });

    confirm.present();
  }
}



